<template>
  <v-container
    fluid
  >
    <base-material-card
      class="px-5 py-3"
      color="indigo"
      icon="mdi-pencil-box-multiple-outline"
      inline
    >
      <template v-slot:after-heading>
        <div class="display-2 font-weight-light">
          {{ $t('快速添加菜品') }}
        </div>
      </template>
      <v-container>
        <v-form
          ref="form"
          v-model="valid"
          :lazy-validation="false"
        >
          <span class="subtitle-1 font-weight-bold">{{ $t('菜品信息') }}</span>
          <template
            v-for="(item, index) of savedItemsList.length"
          >
            <v-row
              :key="index"
              class="mb-0 mt-0"
              dense
            >
              <v-col
                cols="12"
                md="2"
                sm="12"
              >
                <v-text-field
                  v-model="savedItemsList[index].code"
                  :label="$t('code')"
                  :rules="textRules"
                  required
                  dense
                  outlined
                >
                  <template v-slot:append>
                    <div class="red--text">
                      *
                    </div>
                  </template>
                </v-text-field>
              </v-col>
              <v-col
                cols="12"
                md="2"
                sm="12"
              >
                <v-text-field
                  v-model="savedItemsList[index].price"
                  :label="$t('price')"
                  :rules="priceRules"
                  required
                  dense
                  outlined
                >
                  <template v-slot:append>
                    <div class="red--text">
                      *
                    </div>
                  </template>
                </v-text-field>
              </v-col>
              <v-col
                cols="12"
                md="3"
                sm="12"
              >
                <v-text-field
                  v-model="savedItemsList[index].nameDE"
                  :label="$t('nameDE')"
                  :rules="textRules"
                  required
                  dense
                  outlined
                >
                  <template v-slot:append>
                    <div class="red--text">
                      *
                    </div>
                  </template>
                </v-text-field>
              </v-col>
              <v-col
                cols="12"
                md="2"
                sm="12"
              >
                <v-select
                  v-model="savedItemsList[index].printGroupId"
                  :items="printGroupList"
                  item-text="name"
                  item-value="id"
                  :label="$t('printCatName')"
                  :rules="textRules"
                  required
                  dense
                  outlined
                >
                  <template v-slot:append>
                    <div class="red--text">
                      *
                    </div>
                  </template>
                </v-select>
              </v-col>
              <v-col
                cols="12"
                md="2"
                sm="12"
              >
                <v-select
                  v-model="savedItemsList[index].categoryId"
                  :items="categoryList"
                  item-text="_langsname"
                  item-value="id"
                  :label="$t('labelName')"
                  :rules="textRules"
                  required
                  dense
                  outlined
                >
                  <template v-slot:append>
                    <div class="red--text">
                      *
                    </div>
                  </template>
                </v-select>
              </v-col>
              <v-col
                v-if="savedItemsList.length>1"
                cols="12"
                md="1"
                sm="12"
              >
                <v-btn
                  icon
                  x-large
                  color="primary"
                  @click="deleteItem(index)"
                >
                  <v-icon>
                    mdi-delete-circle
                  </v-icon>
                </v-btn>
              </v-col>
            </v-row>
          </template>
          <v-btn
            icon
            large
            class="ml-4"
            color="primary"
            @click="nPlus()"
          >
            <v-icon>
              mdi-pencil-plus
            </v-icon>
            {{ $t('添加菜品') }}
          </v-btn>
        </v-form>
        <v-divider class="mx-2 my-2"/>
        <v-row>
          <v-spacer/>
          <v-btn
            :color="(valid === true)?'primary':'grey'"
            @click="submit()"
          >
            {{ $t('保存') }}
          </v-btn>
        </v-row>
      </v-container>
    </base-material-card>
  </v-container>
</template>

<script>

  import Category from '../../model/Category'
  import PrinterGroup from '../../model/PrinterGroup'
  import DishModel from '../../model/Dish'

  export default {
    data: function () {
      return ({
        valid: true,
        categoryList: [],
        printGroupList: [],
        textRules: [v => !!v || this.$t('Item is required')],
        priceRules: [v => /^[0-9]+\.{0,1}[0-9]{0,2}$/.test(v) || 'Bitte geben Sie den richtigen Preis ein'],
        defaultItem: {
          code: '',
          price: '',
          nameDE: '',
          categoryId: '',
          printGroupId: '',
        },
        langs: [{ lang: 'zh', id: 1, name: '' },
                { lang: 'en', id: 2, name: '' },
                { lang: 'de', id: 3, name: '' }],
        savedItemsList: [],
      })
    },
    async mounted () {
      this.savedItemsList.push(this.defaultItem)
      this.categoryList = await Category.getList()
      this.printGroupList = await PrinterGroup.getList()
    },
    methods: {
      nPlus () {
        this.defaultItem = {
          code: '',
          price: '',
          nameDE: '',
          categoryId: '',
          printGroupId: '',
        }
        this.savedItemsList.push(this.defaultItem)
      },
      deleteItem (idx) {
        this.savedItemsList.splice(idx, 1)
      },
      async submit () {
        if (this.valid === true) {
          for (const item of this.savedItemsList) {
            this.langs.forEach(n => {
              n.name = item.nameDE
            })
            item.langs = this.langs
            await DishModel.multipleAdd(item)
          }
          this.$router.push({
            path: '/dishes/dishes-manage',
          })
        } else {
          this.$refs.form.validate()
        }
      },
    },
  }
</script>
