import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'
import CategoryType from './CategoryType'
import AttributeGroup from './AttributeGroup'
import StringEntity from './StringEntity'
import GlobalSettings from '@/GlobalSettings'
import ConsumeType from './ConsumeType'
import ParentCategoty from './ParentCategoty'

const config = {
  add: function (item) {
    item.appliedAttributeGroup = item.attributeGroupId.join(',')
    item.catTypeId = item.dishesCategoryTypeId
    item.appliedConsumeTypeIds = item.consumeTypeIds.join(',')
    item.appliedFreeInConsumeTypeIds = item.freeInConsumeTypeIds.join(',')
    return hillo.post('Category.php?op=add', {
      ...item,
      langs: JSON.stringify(item.langs),
    }, {
      showLoading: true,
    })
  },
  edit: function (item) {
    item.appliedAttributeGroup = item.attributeGroupId.join(',')
    item.catTypeId = item.dishesCategoryTypeId
    item.appliedConsumeTypeIds = item.consumeTypeIds.join(',')
    item.appliedFreeInConsumeTypeIds = item.freeInConsumeTypeIds.join(',')
    return hillo.postWithUploadFile('Category.php?op=update', {
      ...item,
      langs: JSON.stringify(item.langs),
    }, {
      showLoading: true,
    })
  },
  remove: function (id) {
    return hillo.post('Category.php?op=delete', { id: id })
  },
  load: async function (filter) {
    return (await hillo.get('Category.php', {
      ...filter,
    })).content
  },
}

const entity = {
  id: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    tableConfig: {
      align: 'start',
    },
    formConfig: {
      requiredNew: false,
    },
  },
  categorySort: {
    type: IKDataEntity.Types.Integer,
    displayName: 'sectionSort',
    formConfig: {
      default: 50,
      required: false,
    },
  },
  _langsname: {
    displayName: 'labelName',
    form: false,
  },
  langs: {
    displayName: 'labelName',
    type: IKDataEntity.Types.Group,
    formConfig: {
      groupName: 'labelName',
      default: GlobalSettings.supportLang,
      childLabelKey: 'lang',
      type: {},
    },
    tableConfig: {
      displayCondition: (item) => {
        return item.lang.toUpperCase() === GlobalSettings.getLang()
      },
      displayChild: (key) => {
        return key === 'name'
      },
    },
    childKey: ['name', 'desc'],
    labelKey: 'lang',
    children: [
      StringEntity, StringEntity, StringEntity,
    ],
    header: false,
  },
  dishesCategoryTypeId: {
    displayName: 'labelType',
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: CategoryType.getList,
      },
    },
  },
  attributeGroupId: {
    displayName: 'attributeGroupNames',
    type: IKDataEntity.Types.Option,
    tableConfig: {
      width: '100px',
    },
    formConfig: {
      required: false,
      type: {
        selectItems: AttributeGroup.getList,
        multiple: true,
      },
    },
  },
  consumeTypeIds: {
    type: IKDataEntity.Types.Option,
    formConfig: {
      required: false,
      type: {
        selectItems: ConsumeType.getList,
        multiple: true,
      },
    },
  },
  freeInConsumeTypeIds: {
    type: IKDataEntity.Types.Option,
    formConfig: {
      required: false,
      type: {
        selectItems: ConsumeType.getList,
        multiple: true,
      },
    },
  },
  color: {
    type: IKDataEntity.Types.Color,
    formConfig: {
      required: false,
    },
  },
  printOrder: {
    type: IKDataEntity.Types.Integer,
    required: false,
    formConfig: {
      default: 50,
      requiredNew: false,
      requiredEdit: false,
      rule: [ // ^-[1-9]\d*$
        v => /^[0-9]\d*$/.test(v) || 'Bitte geben Sie die ganze Zahl ein',
      ],
    },
  },
  parentId: {
    type: IKDataEntity.Types.Option,
    formConfig: {
      required: false,
      type: {
        selectItems: ParentCategoty.getList,
        itemText: '_langsname',
      },
    },
    header: false,
  },
}

export default IKDataEntity.ModelFactory(entity, config)
